/*Global*/
body {
    background: #f6f4ef;

    @media(max-width: 767px) {
        padding: 10px;
    }

    @media(min-width: 768px) {
        padding: 30px;
    }
}

.container {
    @media(min-width: 1200px) and (max-width: 1684px) {
        max-width: 1170px;
    }
}

.dark-crema {
    background: #E1DDD2;
}

@media(max-width: 575px) {
    .form-control::placeholder {
        text-transform: none;
        font-size: 14px;
    }
}

select.selectred {
    display: none;
}

.selectric {
    display: flex;
    align-items: center;
    padding: .375rem .98rem;
    border: 0;
    height: 60px;
    background-color: #F6F4EF;

    span.label {
        font-size: 18px;

        @media(max-width: 575px) {
            text-transform: none;
            font-size: 14px;
        }

        font-weight: 400;
        line-height: 1.5;
        color: #938361;
        text-transform: uppercase;
        margin: 0;
        height: auto;
    }

    .button {
        background: transparent;
        top: 50%;
        transform: translate(0, -50%);
    }
}

.selectric-items {
    border: 1px solid #ded7c9;
    background: #F6F4EF;
    box-shadow: none;
}

.selectric-items li {
    padding: 15px;
    font-size: 18px;
    color: #938361;
}

.selectric-items li.highlighted,
.selectric-items li:hover {
    background: #938361;
    color: #fff;
}

/*Header*/
.site-header {
    @media(max-width: 767px) {
        margin-bottom: 15px;
    }

    @media(min-width: 1200px) {
        margin-bottom: 60px;
    }

    @media(min-width: 768px) and (max-width: 1199px) {
        margin-bottom: 30px;
    }
}

.site-header-row {
    @media(max-width: 767px) {
        padding: 10px 0;
    }

    @media(max-width: 1684px) {
        flex-wrap: wrap;
    }

    @media(min-width:768px) and (max-width: 1684px) {
        padding: 40px 15px;
    }

    @media(min-width: 768px) {
        align-items: center;
    }

    @media(min-width:1685px) {
        height: 130px;
    }
}

.header-logo-col {
    @media(max-width: 767px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 5px;
    }
}
.home-header{
	.mobile-menu-toggle{
span{
	background: #fff;
	}
}
}
.mobile-menu-toggle {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: transparent;
    padding: 0;
    border: 0;
    margin-left: 15px;
    min-width: 30px;
    height: 40px;
    padding-top: 11px;

    &:active {
        span {
            background: #b6000b;
        }
    }

    span {
        width: 30px;
        height: 2px;
        background: #181818;
        border-radius: 4px;

        &:not(:last-child) {
            margin-bottom: 6px;
        }
    }
}

.logo {
    @media(max-width: 767px) {
        width: 185px;
        padding-bottom: 5px;
    }

    @media(max-width: 991px) {
        max-width: 100%;
    }
}

.header-menu-col {
    @media(min-width: 768px) and (max-width: 1684px) {
        margin-top: 40px;
    }
}

.header-menu-nav>ul {
    @media(min-width: 768px) {
        display: flex;
    }

    list-style:none;
    padding-left:0;
    margin:0;
}

.header-menu-nav>ul>li {
    &:not(:last-child) {
        @media(min-width: 992px) {
            margin-right: 52px;
        }

        @media(min-width: 768px) and (max-width: 991px) {
            margin-right: 32px;
        }
    }

    @media(max-width: 575px) {
        &.active>a {
            color: #fff;
            background: #b6000b;
        }
    }

    @media(min-width: 576px) {
        &.active>a:before {
            opacity: 1;
        }
    }

}

.header-menu-nav {
    @media(max-width: 767px) {
        margin: 15px -15px 0;

    }

    ul a {
        color: #181818;

        @media(min-width: 768px) {
            font-weight: 500;
        }

    }
}

.header-menu-nav>ul>li>a {
    text-decoration: none;

    @media(min-width: 576px) {
        position: relative;
    }

    @media(max-width: 575px) {
        display: block;
        transition: background-color ease 0.3s, color ease 0.3s;

        &:active {
            color: #fff;
            background: #b6000b;
        }
    }

    @media(max-width: 767px) {
        font-size: 15px;
        padding: 10px 15px;
        text-transform: uppercase;
    }

    @media(min-width: 576px) and (max-width: 767px) {
        display: inline-block;

        &:active {
            &:before {
                opacity: 1;
            }
        }
    }

    @media(min-width: 768px) {

        &:hover,
        &:focus {
            &:before {
                opacity: 1;
            }
        }
    }
}

.header-menu-nav>ul>li>a:before {
    @media(min-width: 576px) {
        content: '';
        opacity: 0;
        position: absolute;

        @media(min-width: 576px) and (max-width: 767px) {
            top: -1px;
            bottom: -1px;
            left: 0;
            right: 0;
        }

        @media(min-width: 768px) and (max-width: 991px) {
            top: -10px;
            bottom: -10px;
            left: -15px;
            right: -15px;
        }

        @media(min-width: 992px) {
            top: -15px;
            bottom: -15px;
            left: -20px;
            right: -20px;
        }

        border:2px solid #F6F4EF;
        transition:opacity ease 0.3s;
    }
}

.header-contacts-col {
    display: flex;

    @media(max-width: 575px) {
        flex-wrap: wrap;
    }

    @media(min-width: 768px) {
        justify-content: right;
    }

    align-items: center;

    @media(min-width: 1685px) {
        order: 1;
    }

    a {
        display: flex;
        align-items: center;

        @media(max-width: 299px) {
            font-size: 15px;
        }

        color: #181818;

        &:hover,
        &:focus {
            color: #b6000b;
        }

        &:not(:last-child) {
            @media(max-width: 767px) {
                margin-right: 15px;
