.project-preview-info-wrap {
    width: 30px;
    height: 30px;
    position: absolute;
    cursor: pointer;

    @media(max-width: 767px) {
        display: none;
    }

    &.bottom {
        .project-preview-info-spacer {
            top: auto;
            bottom: 0;
            padding: 0 0 35px;
        }
    }

    &.right {
        .project-preview-info-spacer {
            left: auto;
            right: 0;
        }
    }

    &:hover {
        .project-preview-info-spacer {
            opacity: 1;
            z-index: 2;

            @media(min-width: 992px) {
                width: 360px;
            }

            @media(min-width: 768px) and (max-width: 991px) {
                width: 280px;
            }
        }

        &:after {
            opacity: 1;
        }
    }

    &:before {
        content: '+';
        position: relative;
        top: 5px;
        left: 5px;
        z-index: 1;
        color: #fff;
        font-size: 18px;
        font-weight: 700;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;
        background: #B6000B;
        border-radius: 100%;
        box-sizing: content-box;
    }

    &:after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 30px;
        height: 30px;
        background: #fff;
        border-radius: 100%;
        opacity: 0.5;
        transition: opacity 0.35s ease;
    }

}

.project-preview-info-spacer {
    width: 0;
    overflow: hidden;
    opacity: 0;
    position: absolute;
    z-index: -1;
    transition: opacity 0.35s ease;
    top: 0;
    left: 0;
    padding-top: 35px;
}

.project-preview-info-content {
    background: #ffff;
    display: flex;

    &:hover,
    &:focus {
